var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-container',[_c('h1',[_vm._v("尼古·弗瑞")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        尼古拉斯·约瑟夫·弗瑞上校是由漫威漫画出版的美国漫画书中出现的一个虚构角色。由作家/艺术家杰克柯比和作家斯坦李创作，弗瑞首次出现在中士。愤怒和他的嚎叫突击队＃1（1963年5月），第二次世界大战的战斗系列，描绘了雪茄煽动愤怒的美国精英部队领导人。一个流行的字符超过数十年来，在2011年，狂怒中排名第33在IGN评出100漫画英雄，和32次在他们的‘前50复仇者’的名单。他有时被认为是反英雄。")]),_c('blockquote',{staticClass:"blockquote"},[_vm._v("        尼克·弗瑞（Nick Fury）出现在多个漫威系列中，以及基于漫画的多部动画电影，电视节目和视频游戏。大卫·哈塞尔霍夫在1998年的电视电影“ 尼克·弗瑞” 中首次描绘了这个角色：神盾局特工塞缪尔·杰克逊后来签署了一张九幅画片，以描绘漫威电影宇宙系列中的角色，首次出现在2008年的电影中钢铁侠。杰克逊也在第一季的两集相关漫威电视节目“神盾局特工”中播出。角色的一个版本出现在漫威的终极漫威印记是基于杰克逊的外表和屏幕角色，就在他被任命之前。杰克逊在电影中描绘的角色的可识别性后来让Marvel退出了原来的角色，取而代之的是他的非洲裔美国儿子Nick Fury Jr.，他喜欢Ultimate Marvel版本的杰克逊。")])]),_c('v-img',{staticClass:"stage-img",attrs:{"width":"195","height":"260","src":""}})],1),_c('br'),_c('h2',[_vm._v("创作背景")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('br'),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        现代Fury，最初是中央情报局特工，几个月后在神奇四侠＃21（1963年12月）中首次亮相。在Strange Tales＃135（1965年8月）中，这个角色被转化为一个间谍，如詹姆斯邦德和虚构的间谍机构神盾局的主要代理人。角色经常出现在漫威书籍中，作为神盾局的前负责人和中间人之间的媒介。美国政府或联合国和各种超级英雄。最终透露，Fury采用了一种名为Infinity Formula的特殊药物，这种药物可以阻止他的衰老，并且尽管已有近一个世纪的历史，但仍然能让他活跃起来。")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        在Strange Tales＃135（1965年8月）中，现在是上校的Fury成为詹姆斯邦德式的冷战间谍，Marvel介绍了秘密组织神盾局（最高总部国际间谍执法部门）及其复仇女神九头蛇。奇怪的故事＃135（1965年8月）。由Jack Kirby和Frank Giacoia封面。\n12页的特写最初由Lee和Kirby提供，后者提供了诸如Helicarrier（一种空中航空母舰）以及人类复制LMD（Life Model Decoys ）甚至汽车 安全气囊这样具有创造性和持久性的小工具和硬件。")])]),_c('v-card',{staticClass